<template>
  <div class="calendar">
    <div class="calendar-box">
      <div class="header clearfix">
        <div class="fl return" @click="back">
          <span></span>
        </div>
        <div class="fl title">日历</div>
        <div class="fr classify">
          <span></span>
        </div>
      </div>

      <div class="date-year">
        <div class="date-weekday clearfix">
          <div class="date fl">{{dateYear.time}} {{dateYear.result.data.holiday}}</div>
          <div class="weekday fr">{{dateYear.result.data.weekday}}</div>
        </div>
        <div class="lunar-calendar clearfix">
          <div class="lunar fl">
            <span>{{dateYear.result.data.animalsYear}}年农历</span>
            <span>{{dateYear.result.data.lunar}}日</span>
          </div>
          <div class="lunar-year fl">{{dateYear.result.data.lunarYear}}</div>
        </div>
        <div class="avoid-suit clearfix">
          <div class="fl avoid">{{dateYear.result.data.avoid}}</div>
          <div class="fl suit">{{dateYear.result.data.suit}}</div>
        </div>
      </div>

      <div class="life">
        <h5 class="life-index">生活指数</h5>
        <div class="index">
          <div class="title-desc clearfix" v-for="(item, index) in life" :key="index">
            <div class="life-icon fl">
              <img class="index-icon" :src="require('../assets/icon/' + item.title + '.png')" alt />
            </div>
            <div class="fl level-desc">
              <div class="title-level clearfix">
                <div class="level fl">{{item.level}}</div>
                <div class="title fr">[ {{item.title}} ]</div>
              </div>
              <div class="desc">{{item.desc}}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";

export default {
  name: "calendar",

  data() {
    return {
      params: {
        key: "120ace86b4db908f9330423dfdd39abe",
        date: "2015-10-10"
      },
      data: {
        appid: "47476821",
        appsecret: "AR7aa0eT",
        version: "v9"
      },
      dateYear: {
        result: {
          data:{}
        }
      },
      life: {},

      //图片路径
      pictureData: []
    };
  },

  methods: {
    // 返回上一步
    back() {
      this.$router.go(-1);
    },
  },

  created() {

    //截取路由参数
    let id = this.$route.query.id;
    // console.log('id :', this.$route.query);

    this.params.date = id
    // return;
    $.ajax({
      type: "GET",
      url: "http://v.juhe.cn/calendar/day",
      data: this.params,
      dataType: "jsonp"
    })
      .then(result => {
        // console.log('result :', result);

        let text = result.result.data.date.split("-");

        result.time = text[0] + "年" + text[1] + "月" + text[2] + "日";
        // console.log('text :', text);
        // console.log("result :", result);
        this.dateYear = result;
        // console.log("this.dateYear :", this.dateYear);
      })
      .catch(err => {
        // console.log("出现错误");
      });

    $.ajax({
      type: "GET",
      url: "https://www.tianqiapi.com/api",
      data: this.data,
      dataType: "jsonp"
    })
      .then(result => {
        // console.log("result :", result);
        this.life = result.data[0].index;
        // console.log('this.life :', this.life);
      })
      .catch(err => {
        // console.log("出现错误");
      });
  }
};
</script>

<style lang="less" scoped>
.calendar {
  color: #fff;
  font-size: 16px;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow-y: auto;
  .calendar-box {
    height: 100%;
  }
  span {
    display: block;
  }
  h5 {
    margin: 0;
  }

  .header {
    text-align: center;
    margin: 10px 12px 0;
    height: 40px;
    line-height: 40px;

    .title {
      width: calc(~"100% - 64px");
      font-size: 18px;
    }
    span {
      width: 32px;
      height: 32px;
      margin-top: 4px;
    }

    .return {
      span {
        background: url("../assets/icon/return.png") no-repeat center center;
        background-size: 100% 100%;
      }
    }

    .classify {
      span {
        background: url("../assets/icon/classify.png") no-repeat center center;
        background-size: 100% 100%;
      }
    }
  }

  .date-year {
    background-color: rgba(255, 255, 255, 0.1);
    box-shadow: 0px 0px 16px 2px rgba(255, 255, 255, 0.55) inset;
    border-radius: 15px;
    padding: 30px 20px;
    // text-align: center;
    margin: 40px 12px;

    .date-weekday,
    .lunar-calendar {
      margin-bottom: 10px;
    }

    .lunar,
    .avoid {
      width: calc(~"50% - 20px");
    }
    .lunar-year,
    .suit {
      width: calc(~"50%");
    }
    .lunar,
    .avoid {
      padding-right: 20px;
    }
  }

  .life {
    margin: 0 12px;
    .life-icon {
      width: 40px;
      height: 40px;

      .index-icon {
        width: 30px;
        height: 30px;
        margin-top: 8px;
        position: relative;
        z-index: 99;
      }
      span {
        margin-top: 8px;
        width: 30px;
        height: 30px;
        background: no-repeat center center;
        background-image: url(../assets/icon/紫外线指数.png);
        background-size: 100% 100%;
      }
    }

    .title-desc {
      margin: 10px 0;
      height: 40px;
    }

    .level-desc {
      width: calc(~"100% - 40px");
    }
    .title-level {
      line-height: 24px;
    }

    .desc {
      font-size: 12px;
      line-height: 16px;
    }
  }
}
</style>